<template>
  <div id="blog" class="blog-content w-100 row m-0" >
        <div class="row m-0 px-0 blog-text-content">
            <p class="mb-4 blog-text title text-center highlight">Últimos posts do blog</p>            
            <div class="col-12 m-0 my-4 p-0 d-flex justify-content-between card-list text-center">
                <div v-for="(item, index) of cardsList" :key="index" class="card-blog">
                    <div class="card-img" :style="`background-image: url(${require('@/assets/banner-cta.png')})`"/>
                    <div class="card-description py-4">
                        <p class="date">{{item.date}}</p>
                        <p class="title">{{item.title}}</p>
                    </div>
                </div>
            </div>
            <div class="w-100 my-4 p-0 d-flex justify-content-center blog-text pagination">
                <DynamicIcon icon="circle-left-arrow" @click="changePage('back')"/> 
                    <p class="mb-0 mt-1 mx-4 px-4">{{page}} / {{totalPages}}</p> 
                <DynamicIcon icon="circle-right-arrow" @click="changePage('next')"/>
            </div>
            <p class="col-12 m-0 mt-4 p-0 blog-text see-more subtitle text-center">
                <u>See all Blog posts</u> 
            </p>
        </div>          
  </div>
</template>

<script>
import DynamicIcon from "@/components/commons/dynamicIcon/DynamicIcon.vue"
//const month = ["January","February","March","April","May","June","July","August","September","October","November","December"]

const cardsMock = [ 
    {id:0, pathImg: "@/assets/banner-cta.png", date: "JULY 01", title: "Welcome DOTE!"}, 
    {id:1, pathImg: "@/assets/banner-cta.png", date: "APRIL 01", title: "Starbucks Rebrand"},
    {id:2,pathImg: "@/assets/banner-cta.png", date: "AUGUST 01", title: "Starbucks Rebrand"}, 
    {id:3, pathImg: "@/assets/banner-cta.png", date: "SEPTEMBER 01", title: "Welcome DOTE!"},
    {id:2,pathImg: "@/assets/banner-cta.png", date: "OCTOBER 01", title: "Welcome DOTE!"}, 
    {id:3, pathImg: "@/assets/banner-cta.png", date: "JANUARY 01", title: "Starbucks Rebrand"},
    {id:2,pathImg: "@/assets/banner-cta.png", date: "MARCH 01", title: "Starbucks Rebrand"}, 
    {id:3, pathImg: "@/assets/banner-cta.png", date: "MAY 01", title: "Welcome DOTE!"},
    ]

export default {
    components:{
        DynamicIcon
    },
    data(){
        return {
            larguraDaTela: window.innerWidth,
            cards: [...cardsMock],
            page: 1,
            size: window.innerWidth <= 950 ? 1 : 2 
        }
    },
    computed:{
        cardsList(){
            const startIndex = (this.page - 1) * this.size;
            const endIndex = startIndex + this.size;
            
            if (startIndex >= this.cards.length) {
                return []; // Retorna um array vazio se a página estiver além dos limites do array.
            }
            
            return this.cards.slice(startIndex, endIndex);
        },
        totalPages(){
            if (this.size <= 0) {
                throw new Error("O tamanho da página deve ser maior que zero.");
            }
            
            return Math.ceil(this.cards.length / this.size);
        },
        isMobile(){
            return this.larguraDaTela <= 950
        }
    },
    methods: {
        changePage( action ){
            if (action == 'back') {
                if( this.page == 1) return
                this.page-- 
            } 

            if (action == 'next') {
                if( this.page == this.totalPages) return
                this.page++ 
            }
        },
        atualizarLarguraDaTela() {
            this.size = window.innerWidth <= 950 ? 1 : 2
        }
    },
    mounted() {
        window.addEventListener("resize", this.atualizarLarguraDaTela);
    },
    beforeDestroy() {
        window.removeEventListener("resize", this.atualizarLarguraDaTela);
    }
}
</script>

<style scoped>
html body .blog-content{
    padding: 25px calc(18%) 50px calc(18%);
    background: linear-gradient(to bottom, #000, #00361b);
}

html body .blog-text-content{
    padding-top: 100px;
    padding-bottom: 50px;
}

html body .blog-text{
    font-weight: 500;
    font-size: 32px;
}

html body .blog-text.title{
    color:#ffffff63;
}

html body .blog-text.highlight{
    font-size: 40px;
}

html body .blog-text.subtitle{
    font-size: 22px;
    color: #ffffff63;
}

html body .blog-text.pagination{
    font-size: 24px;
}

html body .blog-text.see-more, html body .blog-text.pagination img{
    cursor: pointer;
}

html body .blog-text.pagination img:hover{
    transform: scale(1.05);
}

html body .blog-text.see-more{
    padding-right: 10px !important;
}

html body .blog-text.see-more:hover{
    color:#fff
}

html body .card-list .card-blog{
    width: 48%;
}

html body .card-list .card-blog:hover{
    cursor: pointer;
    transform: scale(1.01);
}

html body .card-list .card-blog .card-img{
    height: 350px;
}

html body .card-list .card-blog .card-description{
    background-color: #fff;
    color: #000;
}

html body .card-list .card-blog .card-description .date{
    font-weight: 500;
    color: #000000ad
}

html body .card-list .card-blog .card-description .title{
    font-weight: 600;
    font-size: 32px;
}

@media (max-width: 1250px) {
    .blog-content{
        padding-left: calc(15%);
        padding-right: calc(15%);
    }
}

@media (max-width: 930px) {
    .blog-content{
        padding-left: 30px !important;
        padding-right: 30px !important;
    }

    html body .blog-text-content{
        padding-top: 20px;
        padding-bottom: 15px;
    }

    html body .blog-text.highlight{
        font-size: 24px;
    }

    html body .blog-text.subtitle, html body .blog-text.pagination{
        font-size: 18px;
    }

    html body .blog-text.pagination img{
        height: 30px;
    }

    html body .blog-text.see-more{
        padding-right: 0px !important;
    }

    html body .card-list .card-blog{
        width: 100%;
    }

    html body .card-list .card-blog .card-description .date{
        font-size: 16px;
    }

    html body .card-list .card-blog .card-description .title{
        font-size: 22px;
    }
}
</style>