import Vue from 'vue'
import Router from 'vue-router'
import Home from "@/components/views/Home/Home.vue";

Vue.use(Router)

const routes = [
	{
		name: "Home",
		path: "/",
		component: Home,
	},
	{
		name: "Not Found",
		path: "/*",
		redirect: "/",
	},
];

const router = new Router({ 
      mode: 'history',
      routes 
})

export default router